.Movie_container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.Movie_container div h1 {
  text-align: center;
}

.Movie_movies {
  display: grid;
  grid-template-columns: repeat(2, minmax(400px, 1fr));
  gap: 100px;
  padding: 80px;
  padding-top: 70px;
}

.Movie_movies a {
  text-decoration: none;
}

.Movie_movie {
  display: grid;
  grid-template-columns: minmax(150px, 1fr) 2fr;
  grid-gap: 20px;
  background-color: #fafafa;
  margin-bottom: 10px;
  max-height: 265px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #6c6c6c;
  box-shadow: 0 13px 20px -5px rgba(0, 80, 80, 0.25);
}

.Movie_coverImg {
  position: relative;
  top: -38px;
  max-width: 150px;
  width: 100%;
  height: auto;
  margin-right: 30px;
  box-shadow: 4px 4px 15px 3px rgb(0 0 0 / 25%);
  filter: grayscale(60%);
  transition: all 0.5s;
}

.Movie_title {
  color: black;
  font-weight: 600;
  transition: all 0.3s;
}

.Movie_movie:hover .Movie_title {
  color: #00bfa5;
}

.Movie_movie:hover .Movie_coverImg {
  filter: grayscale(0%);
  max-width: 200px;
}

.Movie_info {
  margin: 10px 0px 0px 0px;
}

.Movie_genres {
  padding: 0px;
  list-style: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin: 0;
}

.Movie_genre {
  margin-right: 8px;
}

@media (max-width: 1024px) {
  .Movie_movies {
    grid-template-columns: repeat(1, minmax(400px, 1fr));
    grid-gap: 50px;
  }
}
