.detail_container {
  height: 100%;
}

.Detail_background {
  width: 100%;
  filter: brightness(50%);
  max-height: 500px;
  object-fit: cover;
}

.Detail_container {
  display: flex;
  padding: 0 5vw;
}

.Detail_cover {
  /* z-index: 10;
  top: 500px;
  left: 50px; */
  position: relative;
  width: 200px;
  height: 300px;
  top: -120px;
  margin-left: 3vw;
  box-shadow: 4px 4px 15px 3px rgb(0 0 0 / 25%);
}

.Detail_text {
  margin-left: 3vw;
}

.Detail_genres {
  padding: 0px;
  list-style: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin: 0;
}

.Detail_genre {
  margin-right: 8px;
}
